<template>
  <h1 class="m-5">Edit Blog</h1>
  <div class="m-5">
    <div class="alert alert-success" role="alert" v-if="published">
      Your Blog is Edited Successfully!<br>
    </div>

    <div class="input-group input-group-lg">
      <span class="input-group-text" id="inputGroup-sizing-lg">Blog Title</span>
      <input type="text" class="form-control" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-lg" v-model="blog_title">
    </div>

    <br>

    <div class="form-floating">
      <textarea rows="10" style="height:100%;" class="form-control" placeholder=" content here" id="floatingTextarea" v-model="blog_content"></textarea>
      <label for="floatingTextarea">Blog Content</label>
    </div>
    <br>
    <div>
      <button class="btn btn-outline-warning btn-lg" @click="edit_blog" v-if="!publishing">Edit</button>
      <center>
        <div class="spinner-grow text-success" role="status" v-if="publishing">
          <span class="visually-hidden">Loading...</span>
        </div>
      </center>


    </div>

  </div>



</template>

<script>
import {useRoute} from 'vue-router'
import { updateDoc,doc} from "firebase/firestore";
import {firestore_db} from "@/main";
import router from "@/router";

export default {
  name: "EditView",
  data(){
    return {
      blog_id: '',
      blog_title: '',
      blog_content: '',
      publishing: false,
      published: false
    }
  },
  mounted() {
    const route = useRoute();
    this.blog_id = route.params.blog_id;
    this.blog_title = route.params.blog_title;
    this.blog_content = route.params.blog_content;
  },
  methods:{
    async edit_blog() {
      this.publishing = true;
      const blogRef = doc(firestore_db, "blogs", this.blog_id);

      await updateDoc(blogRef, {
        title: this.blog_title,
        content: this.blog_content
      }).then((result)=>{
        console.log(result)
        alert("edited!")
        router.push('/')
      }).catch((err)=>{
        alert(err.message)
        this.publishing = false;
      });
    }
  }
}
</script>

